import React from "react";
import table from '../poker-7174110.png';
import { IconButton } from 'rsuite';
import { Plus, UpdateRound } from '@rsuite/icons'
import "rsuite/dist/rsuite.min.css";

function importAll(r) {


  let images = {};
  r.keys().map((item) => { images[item.replace('./', '')] = r(item); return item});
  return images;
}

const images = importAll(require.context('../PlayingCards', false, /\.(png|jpe?g|svg)$/));


function Image(props){


  function showPlayerCards(hand){
    return <div>
      <img src = {images[hand.card1 + ".png"]} className={"player" + hand.playerNum + "-card1"} alt= ""></img>
      <img src = {images[hand.card2 + ".png"]} className={"player" + hand.playerNum + "-card2"} alt= ""></img>
    </div>
  }
  function showFlopCards(hand) {
    return <div>
      <img src = {images[hand[0] + ".png"]} className = "flop-card1" alt= ""></img>
      <img src = {images[hand[1] + ".png"]} className = "flop-card2" alt= ""></img>
      <img src = {images[hand[2] + ".png"]} className = "flop-card3" alt= ""></img>
      <img src = {images[hand[3] + ".png"]} className = "turn-card" alt= ""></img>
      <img src = {images[hand[4] + ".png"]} className = "river-card" alt= ""></img>
    </div>
  }
    const ButtonStyle = { margin: "0px 10px",
    borderRadius: "50%"};

    return <div className="img-container">
    <div className="table">
        <img src = {table} alt = "table" className="image"></img>
        {props.playerHand.map(showPlayerCards)}
        {showFlopCards(props.flopHand)}
        <IconButton id = "add" icon={<Plus />} color="cyan"
        appearance="primary" style = {ButtonStyle} className = 'button1' 
          onClick = {() => props.handleTheClick(1)}
        />
                <IconButton id = "add" icon={<Plus />} color="cyan"
        appearance="primary" style = {ButtonStyle} className = 'button2' 
          onClick = {() => props.handleTheClick(2)}
        />
                <IconButton id = "add" icon={<Plus />} color="cyan"
        appearance="primary" style = {ButtonStyle} className = 'button3' 
          onClick = {() => props.handleTheClick(3)}
        />
                <IconButton id = "add" icon={<Plus />} color="cyan"
        appearance="primary" style = {ButtonStyle} className = 'button4' 
          onClick = {() => props.handleTheClick(4)}
        />
                <IconButton id = "add" icon={<Plus />} color="cyan"
        appearance="primary" style = {ButtonStyle} className = 'button5' 
          onClick = {() => props.handleTheClick(5)}
        />
                <IconButton id = "add" icon={<Plus />} color="cyan"
        appearance="primary" style = {ButtonStyle} className = 'button6' 
          onClick = {() => props.handleTheClick(6)}
        />
                <IconButton id= "add" icon={<Plus />} color = "cyan"
        appearance= "primary" style={ButtonStyle} className = 'buttonFlop'
          onClick= {() => props.handleTheClick(7)}
        />
        <IconButton id = "add" icon={<UpdateRound />} color='orange'
        appearance="primary" className = 'calcButton' 
          onClick = {event => {props.calculate();}}
        />
        </div>
    </div>;
}

export default Image;