import React from "react";


function importAll(r) {
  let images = {};
  r.keys().map((item, index) => { images[item.replace('./', '')] = r(item); });
  return images;
}
const images = importAll(require.context('../PlayingCards', false, /\.(png|jpe?g|svg)$/));

function Card(props){
  return (
  <input onClick = {() => props.changeHand(props.val)} className = "card" type="image" src= {images[props.src]} />
  )
}

export default Card