import { calculateEquity } from 'poker-odds'

//const calculateEquity = require('poker-odds').calculateEquity;

const hands = [['Ac', 'Kh'], ['Kd', 'Qs']]
const board = []
const iterations = 100000 // optional
const exhaustive = false // optional

function runSimulation(hands, board, iterations = 100000, exhaustive = false){

    const arr = calculateEquity(hands, board, iterations, exhaustive);
    const winArr = [];
    for (const hand of arr) {
        let winPerc = (hand.wins/hand.count * 100).toFixed(2);
        let tiePerc = (hand.ties/hand.count * 100).toFixed(2);
        winArr.push([winPerc,tiePerc]);
    }

    return winArr;
}

const test = runSimulation(hands,board,iterations,exhaustive);
console.log(test[0][0])

export default runSimulation;